<template>
    <div class="elementui-demo-item">
        <h3>MessageBox </h3>
        <div>
            <el-button-group>
                <el-button type="text" @click="open">MessageBox</el-button>
                <el-button type="text" @click="confirm">confirm</el-button>
                <el-button type="text" @click="prompt">prompt</el-button>
            </el-button-group>
        </div>
    </div>
</template>
<script>
export default {
    methods: {
        open () {
            this.$alert('', '', {
                confirmButtonText: '',
                callback: action => {
                    this.$message({
                        type: 'info',
                        message: `action: ${action}`
                    })
                }
            })
        },
        confirm () {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '!'
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })
            })
        },
        prompt () {
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
                inputErrorMessage: '',
                center: true,
                roundButton: true
            }).then(({ value }) => {
                this.$message({
                    type: 'success',
                    message: ': ' + value
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })
            })
        }
    }
}
</script>
<style lang="scss" scoped>
@import '~styles/el-demo-item.scss';
</style>



