<template>
    <div class="elementui-demo-item">
        <h3>Notification </h3>
        <div>
            <el-button-group>
                <el-button type="primary" @click="open"></el-button>
                <el-button type="primary" @click="open2"></el-button>
                <el-button type="primary" @click="open3"></el-button>
                <el-button type="primary" @click="open4"></el-button>
                <el-button type="primary" @click="close"></el-button>
            </el-button-group>
        </div>
    </div>
</template>
<script>
export default {
    methods: {
        close () {
            this.instance.close()
        },
        handleClose () {
            console.log('')
        },
        handleClick () {
            console.log('')
        },
        open () {
            const h = this.$createElement

            this.$notify({
                type: 'success',
                title: '',
                message: h('i', { style: 'color: teal' }, ''),
                onClose () {
                    console.log('close')
                }
            })
        },
        open2 () {
            this.instance = this.$notify.warning({
                title: '',
                message: '',
                duration: 0,
                offset: 100,
                onClick () {
                    console.log('click')
                }
            })
        },
        open3 () {
            this.$notify.info({
                title: '',
                message: '',
                duration: 1000,
                position: 'top-left',
                showClose: false
            })
        },
        open4 () {
            this.$notify({
                type: 'error',
                title: '',
                message: '',
                position: 'bottom-right'
            })
        }
    }
}
</script>
<style lang="scss" scoped>
@import '~styles/el-demo-item.scss';
</style>



